'use client';

import React, { useState, useEffect } from 'react';

export default function Home() {
  const [date, setDate] = useState('');
  const [agenceId, setAgenceId] = useState('');
  const [prestationId, setPrestationId] = useState('');
  const [cout, setCout] = useState('');
  const [successMessage, setSuccessMessage] = useState('');
  const [agencesList, setAgencesList] = useState<{ id: number; name: string }[]>([]);
  const [prestationsList, setPrestationsList] = useState<{ id: number; name: string }[]>([]);

  useEffect(() => {
    const fetchAgences = async () => {
      const res = await fetch('http://localhost:3001/api/agences');
      const data = await res.json();
      console.log('data : ', data)
      setAgencesList(data);
    };
  
    const fetchPrestations = async () => {
      const res = await fetch('http://localhost:3001/api/prestations-types'); // ou prestations selon ton backend
      const data = await res.json();
      setPrestationsList(data);
    };
  
    fetchAgences();
    fetchPrestations();
  }, []);
  

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    const payload = {
      agenceId: parseInt(agenceId),
      prestationId: parseInt(prestationId),
      date,
      cout: parseFloat(cout)
    };

    const res = await fetch('http://localhost:3001/api/prestations', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(payload)
    });

    const data = await res.json();

    if (data.message) {
      setSuccessMessage("üéâ Prestation enregistr√©e avec succ√®s !");
    }

    setAgenceId('');
    setDate('');
    setPrestationId('');
    setCout('');
  };

  // Auto-fermeture du message apr√®s 5 secondes
  useEffect(() => {
    if (successMessage) {
      const timer = setTimeout(() => {
        setSuccessMessage('');
      }, 5000);
      return () => clearTimeout(timer);
    }
  }, [successMessage]);

  return (
    <main style={styles.container}>
      <img src="/logo.webp" alt="Logo Transvie" style={styles.logo} />
      <h2 style={styles.title}>Enregistrement de prestation</h2>

      {successMessage && (
        <div style={styles.successMessage}>
          <span>{successMessage}</span>
          <button type="button" style={styles.closeButton} onClick={() => setSuccessMessage('')}>
            ‚úñ
          </button>
        </div>
      )}

      <form onSubmit={handleSubmit} style={styles.form}>
        <label>Agence</label>
        <select required value={agenceId} onChange={(e) => setAgenceId(e.target.value)} style={styles.input}>
          <option value="">-- Choisissez une agence --</option>
          {agencesList.map((a) => (
            <option key={a.id} value={a.id}>{a.name}</option>
          ))}
        </select>

        <label>Date</label>
        <input type="date" required value={date} onChange={(e) => setDate(e.target.value)} style={styles.input} />

        <label>Prestation</label>
        <select required value={prestationId} onChange={(e) => setPrestationId(e.target.value)} style={styles.input}>
          <option value="">-- Choisissez une prestation --</option>
          {prestationsList.map((p) => (
            <option key={p.id} value={p.id}>{p.name}</option>
          ))}
        </select>

        <label>Co√ªt (FCFA)</label>
        <input
          type="number"
          required
          value={cout}
          onChange={(e) => setCout(e.target.value)}
          style={styles.input}
        />

        <button type="submit" style={styles.button}>Enregistrer</button>
      </form>
    </main>
  );
}

const styles: { [key: string]: React.CSSProperties } = {
  container: {
    maxWidth: '500px',
    margin: '40px auto',
    padding: '30px',
    backgroundColor: '#f9f9f9',
    borderRadius: '10px',
    boxShadow: '0 3px 15px rgba(0,0,0,0.1)',
    fontFamily: 'Segoe UI, sans-serif',
    color: '#344767',
    position: 'relative'
  },
  logo: {
    display: 'block',
    margin: '0 auto 20px',
    maxWidth: '150px',
    height: 'auto'
  },
  title: {
    textAlign: 'center',
    color: '#007bff',
    marginBottom: '20px'
  },
  form: {
    display: 'flex',
    flexDirection: 'column',
    gap: '15px'
  },
  input: {
    padding: '10px',
    fontSize: '16px',
    borderRadius: '6px',
    border: '1px solid #ccc',
    backgroundColor: '#ffffff',
    color: '#344767'
  },
  button: {
    padding: '12px',
    backgroundColor: '#007bff',
    color: 'white',
    border: 'none',
    borderRadius: '6px',
    fontSize: '16px',
    fontWeight: 600,
    cursor: 'pointer'
  },
  successMessage: {
    backgroundColor: '#d4edda',
    color: '#155724',
    padding: '15px 20px',
    borderRadius: '6px',
    marginBottom: '20px',
    fontSize: '16px',
    textAlign: 'center',
    position: 'relative'
  },
  closeButton: {
    position: 'absolute',
    top: '10px',
    right: '12px',
    background: 'transparent',
    border: 'none',
    fontSize: '18px',
    fontWeight: 'bold',
    color: '#155724',
    cursor: 'pointer'
  }
};
